
.home-layout {
    border: 0;
    padding: 0;
    margin: 0;
    width: 100%;
}
.home-layout-top {
    padding: 0;
    margin: 0;
    width: 100%;
    height: 80px;
    background-color: #ffffff;
    border-bottom: 1px solid #e1e1e1;
    -webkit-box-shadow: 0 1px 1px #14000000;
            box-shadow: 0 1px 1px #14000000;
}
.home-layout-logo {
    width: auto;
    line-height: 50px;
    height: 50px;
    text-align: center;
    font-size: 20px;
    color: rgb(82, 168, 255);
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 90px;
}
.home-layout-nav {
  width: auto;
line-height: 50px;
height: 50px;
text-align: center;
font-size: 16px;
color: #999;
float: right;
right: 100px;
top: 15px;
position: absolute;
}
.home-layout-content {
    height: auto;
    margin: 30px 50px;
    overflow: hidden;
    background: #fff;
    border-radius: 10px;
    -moz-border-radius: 10px;
    -webkit-border-radius: 10px;
}
.home-layout-content-menu {
    width: 300px;
    height: auto;
    border-right: solid 1px #dddee1;
    float: left;
    margin-bottom: -10000px;
    padding-bottom: 10000px;
}
.home-layout-content-main {
    text-align: left;
    padding: 15px;
    margin-left: 300px;
    height: auto;
    margin-bottom: -10000px;
    padding-bottom: 10015px;
}
.home-layout-copy {
    text-align: center;
    border: 0;
    padding: 0 0 10px 0;
    color: #9ea7b4;
    width: 100%;
}
.auto-search{
  width:250px !important;
  margin-left: 20px;
}
.demo-auto-complete-item{
  padding: 4px 0;
  border-bottom: 1px solid #F6F6F6;

  cursor: pointer;
}
.demo-auto-complete-group{
  font-size: 6px;
  padding: 4px 6px;
}
.demo-auto-complete-group span{
  color: #666;
  font-weight: bold;
}
.demo-auto-complete-group span.search-result-title{
  color: #5cadff;
}
.ivu-select-dropdown{
  z-index: 9999 !important;
}
.badge-finish{
  background:  #5cb85c !important;
}
.badge-wrapper .ivu-tag-text{
  color:  #5cb85c !important;
}



